.gallerycontainer {
  position: fixed;
  background-color: #222222;
  height: 400px;
  top: 0;
  height: 65vh;
  width: calc(16vw);
  margin: 0 0px 0 calc(83.5vw - 400px);
  overflow: hidden;
}

.arrow:hover {
  background-color: #a9a9a9;
  color: #222222;
}

.arrow {
  width: calc(16vw);
  height: 25px;
  background-color: #161616;
  font-size: 18px;
  line-height: 25px;
  color: #a9a9a9;
}

.bottomArrow {
  position: absolute;
  bottom: 0;
}

.user {
  background-color: #2e2e2e;
  width: calc(15.5vw);
  height: 125px;
  margin: 10px 0.25vw;
  border-radius: 5px;
  line-height: 125px;
  font-size: 20px;
  color: #a9a9a9;
}
